.vacancies {
    background-color: #FFFFFF;
    border-radius: 8px;
}

.search {
    display: flex;
    align-items: center;
    border: 1px solid #EAEBED;
    border-radius: 8px;
}

.input {
    width: 80%;
}

.button {
    width: 20%;
}

.pagination {
    display: flex;
    justify-content: center;
}

.loader {
    display: flex;
    margin: 0 auto;
    height: 70vh;
}